@charset "UTF-8";
html * {
  margin: 0;
  padding: 0;
  border: none;
  color: #333333;
  box-sizing: border-box; }

html body {
  font-size: 14px;
  min-width: 320px;
  font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  background-image: -webkit-linear-gradient(60deg, rgba(255, 165, 150, 0.5) 5%, rgba(0, 228, 255, 0.35)), url(.././assets/bg.jpg?342e899576d05c1363859ead161d3199);
  background-size: cover; }

a {
  text-decoration: unset; }

ul {
  list-style-type: none; }

p {
  text-align: justify; }

.el-message-box {
  width: 300px !important;
  min-width: 300px !important; }

.el-message {
  width: 80% !important;
  min-width: 200px !important;
  max-width: 380px !important; }

.clearBoth {
  zoom: 1; }
  .clearBoth::before, .clearBoth::after {
    content: '';
    display: table;
    clear: both;
    visibility: hidden;
    overflow: hidden; }

::-webkit-input-placeholder {
  color: gray; }

:-ms-input-placeholder {
  color: gray; }

::placeholder {
  color: gray; }

.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden; }

